<template>
  <div style="background: #fff;position: absolute;width: 100%;height: 100%">
    <Header></Header>
    <div class="title">
      Sign up
    </div>
    <div class="stepProgress">
      <div class="step active">step 1</div>
      <div class="step">step 2</div>
      <div class="step">step 3</div>
    </div>
    <div class="text">
      input your mobile phone
    </div>
    <div class="phoneBox">
      <div class="region" @click="changeRegion">
        <span >+{{defaultRegionNumber}}</span><span class="down-arrow"></span>
      </div>
      <div class="phoneNumber">
        {{phoneNumber}}
      </div>
    </div>

    <div class="keyboard">
      <div class="keyItem" v-for="item in keyCodes" v-bind:key="item" @click="clickItem">
        {{item}}
      </div>
    </div>

    <div class="submitBox">
      <div class="submitBtn" @click="submit">
        submit
      </div>
    </div>

    <div class="regionMask" v-if="showRegions" @click="closeRegionModal"></div>
    <div class="regionBox" v-if="showRegions">
      <div class="regionContainer">
        <p class="countryTitle">Country / Region</p>
        <div class="splitLine"></div>
        <div class="regionList">
          <div class="regionItem"
               v-for="item in regions" v-bind:key="item.regionName"
               @click="clickRegionItem"
               :data-regionNumber="item.regionNumber"
          >
            <img class="regionPic" :src="item.countryFlagSmallUrl" :data-regionNumber="item.regionNumber"/>
            <div class="regionName" :data-regionNumber="item.regionNumber">
              {{item.name}}
            </div>
            <div class="regionNumber" :data-regionNumber="item.regionNumber">
              +{{item.callingCode}}
            </div>
          </div>
        </div>
      </div>
      <div @click="closeRegionModal">
        <div class="cancelBtn" @click="closeRegionModal">X</div>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  import { mapGetters } from 'vuex'
  import Header from '../components/Header'
export default {
  name: 'Index',
  data () {
    return {
      showRegions:false,
      defaultRegionNumber:"86",
      keyCodes:["1","2","3","4","5","6","7","8","9","cancel","0","X"],
      phoneNumber:"",
      blockFlag:true,
    }
  },
  computed: {
    ...mapGetters([
      'regions', ,
    ])
  },
  components: {
    Header
  },
  mounted () {
  },
  methods: {
    clickItem(e){
      $('.keyboard').find('.keyItem').removeClass('active');
      let item = e.target;
      $(item).addClass('active');
      setTimeout(()=>{
        $(item).removeClass('active');
      },600)
      let value = e.target.innerText
      let phoneNumber = this.phoneNumber;
      if(value=="cancel"){
        this.phoneNumber="";
      }else if(value=="X" || value=="x"){
        if(phoneNumber.length>0){
          phoneNumber = phoneNumber.substring(0,phoneNumber.length-1)
          this.phoneNumber=phoneNumber;
        }
      }else{
        this.phoneNumber=phoneNumber+value;
      }
    },
    submit(){
      let phoneNumber = this.phoneNumber;
      if(phoneNumber !=""){
        this.$router.push('/RegisterStepTwo')
      }
    },
    changeRegion(){
      this.showRegions = true;
    },
    closeRegionModal(){
      this.showRegions = false;
    },
    clickRegionItem(e){
      $('.regionList').find('.regionItem').removeClass('regionItemActive');
      $(e.target).parent().addClass('regionItemActive');
      setTimeout(()=>{
        $(e.target).removeClass('regionItemActive');
      },600);
      if(e.target.dataset.regionnumber){
        this.defaultRegionNumber = e.target.dataset.regionnumber
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  @import '../css/common';
  @import '../css/registerStepOne';
</style>



