export const Store = {
  state:{
    regions:[]
  },
  getters:{
    regions:(state)=>{
      return state.regions;
    }
  },
  actions:{
    setRegions:(context,payload)=>{
      context.commit('setRegions',payload)
    },
  },
  mutations:{
    setRegions(state,payload){
      state.regions = payload.regions;
    },
  }
};



